<script>
    import "swiper/dist/css/swiper.css";
    import { swiper, swiperSlide } from "vue-awesome-swiper";
    import echarts from "echarts";
    require("echarts/map/js/china.js");
    let mapOptions = require("../../assets/js/index/mapOptions");

    export default {
        name: "homeDefault",
        data() {
            return {
                show: 1,
                accordionIndex: 0,
                proSwiperIndex: 0,
                fangData: [
                    {
                        title: "",
                        sub: "",
                        des:
                            "\
                             ",
                        num: [1, 2, 3],
                        imgs: [
                            ['',require("../../assets/images/index/home/xiangmu1-1.png")],
                            ['',require("../../assets/images/index/home/xiangmu1-2.png")],
                            ['',require("../../assets/images/index/home/xiangmu1-3.png")]],
                    },
                    {
                        title: "",
                        sub: "",
                        des:
                            "\
                             ",
                        num: [1, 2, 3],
                        imgs: [
                            ['',require("../../assets/images/index/home/xiangmu1-1.png")],
                            ['',require("../../assets/images/index/home/xiangmu1-2.png")],
                            ['',require("../../assets/images/index/home/xiangmu1-3.png")]],
                    },
                    {
                        title: "",
                        sub: "",
                        des:
                            "\
                             ",
                        num: [1, 2, 3],
                        imgs: [
                            ['',require("../../assets/images/index/home/xiangmu1-1.png")],
                            ['',require("../../assets/images/index/home/xiangmu1-2.png")],
                            ['',require("../../assets/images/index/home/xiangmu1-3.png")]],
                    },
                    {
                        title: "",
                        sub: "",
                        des:
                            "\
                            ",
                        num: [1, 2, 3],
                        imgs: [
                            ['',require("../../assets/images/index/home/xiangmu1-1.png")],
                            ['',require("../../assets/images/index/home/xiangmu1-2.png")],
                            ['',require("../../assets/images/index/home/xiangmu1-3.png")]],
                    }
                ],
                accordion: [
                    {
                        imgs: require("../../assets/images/index/home/fengqin02.png"),
                        name: "",
                        icons: require("../../assets/images/index/home/icons_guangai.png")
                    },
                    {
                        imgs: require("../../assets/images/index/home/fengqin01.png"),
                        name: "",
                        icons: require("../../assets/images/index/home/icons_renyin.png")
                    },
                    {
                        imgs: require("../../assets/images/index/home/fengqin03.png"),
                        name: "",
                        icons: require("../../assets/images/index/home/icons_jijing.png")
                    },
                    {
                        imgs: require("../../assets/images/index/home/fengqin04.png"),
                        name: "",
                        icons: require("../../assets/images/index/home/icons_diguan.png")
                    },
                    {
                        imgs: require("../../assets/images/index/home/fengqin05.png"),
                        name: "",
                        icons: require("../../assets/images/index/home/icons_hezhang.png")
                    },
                    {
                        imgs: require("../../assets/images/index/home/fengqin06.png"),
                        name: "",
                        icons: require("../../assets/images/index/home/icons_shuiwen.png")
                    },
                    {
                        imgs: require("../../assets/images/index/home/fengqin09.png"),
                        name: "",
                        icons: require("../../assets/images/index/home/icons_shanhong.png")
                    }
                ],
                swiperOption: {
                    effect: "fade",
                    setShowIndex: currIndex => {
                        var cur = currIndex;
                        if (currIndex === 5) {
                            cur = 1;
                        }
                        this.show = cur;
                    },
                    // speed: 10000,
                    loop: true,
                    autoplay: {
                        delay: 10000
                    },
                    pagination: {
                        el: ".swiper-pagination"
                    },
                    navigation: {
                        nextEl: ".swiper-button-next",
                        prevEl: ".swiper-button-prev"
                    },
                    on: {
                        transitionStart: function() {
                            this.params.setShowIndex(this.activeIndex);
                        },
                        slideChange: function() {
                            this.params.setShowIndex(this.activeIndex);
                        },
                        slideChangeTransitionStart: function() {
                            this.params.setShowIndex(this.activeIndex);
                        }
                    }
                },
                proSwiperOption: {
                    effect: "speed",
                    direction: 'vertical',
                    setShowIndex: currIndex => {
                        var cur = currIndex;
                        if (currIndex > this.fangData.length - 1) {
                            cur = 0;
                        }
                        this.proSwiperIndex = cur;
                    },
                    loop: true,
                    autoplay: {
                        delay: 3000
                    },
                    pagination: {
                        el: ".swiper-pagination"
                    },
                    navigation: {
                        nextEl: ".swiper-button-next",
                        prevEl: ".swiper-button-prev"
                    },
                    on: {
                        transitionStart: function() {
                            this.params.setShowIndex(this.activeIndex - 1);
                        },
                        slideChange: function() {
                            this.params.setShowIndex(this.activeIndex - 1);
                        },
                        slideChangeTransitionStart: function() {
                            this.params.setShowIndex(this.activeIndex - 1);
                        }
                    }
                }
            };
        },
        methods: {
            changeTab0() {
                this.show = 1;
                this.swiper.slideTo(1, 1000, false);
            },
            changeTab1() {
                this.show = 2;
                this.swiper.slideTo(2, 1000, false);
            },
            changeTab2() {
                this.show = 3;
                this.swiper.slideTo(3, 1000, false);
            },
            changeTab3() {
                this.show = 4;
                this.swiper.slideTo(4, 1000, false);
            },
            accMouseEnter(index) {
                this.accordionIndex = index;
            },
            hoverChangeSlider(index){
                this.proSwiperIndex = index;
                this.swiper2.slideTo(index+1, 300, false);
            },
            drawCharts() {
                let myChart = echarts.init(document.getElementById("myMap")); // 
                myChart.setOption(mapOptions.option);
                window.onresize = function() {
                    myChart.resize();
                };
            }
        },
        mounted() {
            this.drawCharts();
        },
        computed: {
            swiper() {
                return this.$refs.mySwiper.swiper;
            },
            swiper2() {
                return this.$refs.mySwipers.swiper;
            }
        },
        components: {
            swiper,
            swiperSlide
        }
    };
</script>

<style lang="scss" scoped>
    $activeColor: #00f6ff;
