                font-size: 55px;
                margin-top: 44px;
            }
            p {
                font-size: 22px;
                margin-top: 32px;
            }
        }
        .btn-container {
            .btn-icon {
                border: 1px solid #1e409c;
                height: 44px;
                width: 44px;
                border-radius: 4px;
                text-align: center;
                line-height: 54px;
                margin: 40px 0 0 6px;
            }
            .btn-des {
                color: #1e409c;
            }
            .btn-des.active {
                color: $activeColor;
            }
            $activePosition: 0;
            .btn-icon > i {
                display: inline-block;
                height: 22px;
                width: 22px;
                background: url("../../assets/images/index/home/icons.png") no-repeat;
            }
            .btn-icon.system i {
                background-position: -66px -25px;
            }
            .btn-icon.agreement i {
                background-position: -22px -25px;
            }
            .btn-icon.project i {
                background-position: 0 -25px;
            }
            .btn-icon.monitoring i {
                background-position: -44px -25px;
            }
            @mixin btnsActive() {
                .btn-icon {
                    border-color: $activeColor;
                    transition: border-color 0.7s ease;
                }
                .btn-icon.system i {
                    background-position: -66px 0;
                }
                .btn-icon.agreement i {
                    background-position: -22px 0;
                }
                .btn-icon.project i {
                    background-position: 0 0;
                }
                .btn-icon.monitoring i {
                    background-position: -44px 0;
                }
                .btn-des {
                    color: $activeColor;
                    transition: color 0.7s ease;
                }
            }
            .btns {
                cursor: pointer;
                &:hover {
                    .btn-des {
                        color: $activeColor;
                    }
                    @include btnsActive();
                }
                &.active {
                    @include btnsActive();
                }
            }
        }
        .know-more {
            padding: 10px 30px;
            border: 1px solid #1859c0;
            color: #1859c0;
            display: inline-block;
            border-radius: 4px;
            margin-top: 130px;
            cursor: pointer;
            &:hover {
                color: $activeColor;
                border-color: $activeColor;
            }
        }
    }
    //
    .icon-lines {
        display: inline-block;
        width: 50px;
        height: 3px;
        background: #108cee;
    }
    // 
